import Vue from 'vue'
import VueRouter from 'vue-router'
import App from './App.vue'
import CustomComponent from './components/CustomComponent'

Vue.use(VueRouter);

const routes = [
  {
    name: 'home',
    path: '/',
    component: CustomComponent,
  }
]

Vue.config.productionTip = false

const router = new VueRouter({
  mode: 'history',
  routes,
})

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
